<template>
  <Header class="detailHeader">
    <div slot="left" class="detailHeader-left" @click.stop="back"></div>
    <p slot="center" class="detailHeader-title">{{title}}</p>
    <div slot="right" class="detailHeader-right"></div>
  </Header>
</template>

<script>
import Header from '../Header'
import { mapActions } from 'vuex'
export default {
  name: 'DetailHeader',
  components: {
    Header
  },
  methods: {
    ...mapActions([
      'setListScreen'
    ]),
    back () {
      window.history.back()
      this.setListScreen(false)
    }
  },
  props: {
    title: {
      type: String,
      default: '',
      required: true
    }
  }
}
</script>

<style scoped lang="scss">
@import "../../assets/css/variable";
@import "../../assets/css/mixin";
.detailHeader{
  position: relative;
  z-index: 100;
  .detailHeader-left{
    @include bg_img('../../assets/images/back')
  }
  .detailHeader-right{
    @include bg_img('../../assets/images/more')
  }
  .detailHeader-title{
    text-align: center;
    line-height: 100px;
    color: white;
    font-weight: bold;
    @include font_size($font_medium);
    @include no-wrap();
  }
}
</style>
