<template>
  <div class="detail">
    <DetailHeader :title="playlist.name"></DetailHeader>
    <DetailTop :path="playlist.image" ref="top"></DetailTop>
    <div class="bottom">
      <ScrollView ref="scrollView">
        <DetailButton :songs="playlist.songs"></DetailButton>
      </ScrollView>
    </div>
  </div>
</template>

<script>
import DetailHeader from '../components/Detail/DetailHeader'
import DetailTop from '../components/Detail/DetailTop'
import DetailButton from '../components/Detail/DetailButton'
import ScrollView from '../components/ScrollView'
import { getPlayList, getAlbum, getArtistsSongs } from '../api/index'
export default {
  name: 'Detail',
  components: {
    DetailHeader,
    DetailTop,
    DetailButton,
    ScrollView
  },
  data: function () {
    return {
      playlist: {
        name: '',
        image: '',
        songs: []
      }
    }
  },
  created () {
    // 修改时间2021/5/6
    // console.log(this.$route.params.type)
    if (this.$route.params.type === 'personalized') {
      getPlayList({ id: this.$route.params.id })
        .then((data) => {
        // console.log(data)
          this.playlist.name = data.playlist.name
          this.playlist.image = data.playlist.coverImgUrl
          const list = []
          data.playlist.tracks.forEach((value1) => {
            const obj = {}
            obj.songID = value1.id
            obj.songName = value1.name
            obj.singer = ''
            value1.ar.forEach(function (value2, index) {
              if (index === 0) {
                obj.singer = value2.name
              } else {
                obj.singer += '-' + value2.name
              }
            // console.log(obj.singer)
            })
            list.push(obj)
            this.playlist.songs = list
          })
        })
        .catch(() => {
          console.log('获取推荐歌单数据失败')
        })
    } else if (this.$route.params.type === 'albums') {
      getAlbum({ id: this.$route.params.id })
        .then((data) => {
        // console.log(data)
          this.playlist.name = data.album.name
          this.playlist.image = data.album.picUrl
          const list = []
          data.songs.forEach((value1) => {
            const obj = {}
            obj.songID = value1.id
            obj.songName = value1.name
            obj.singer = ''
            value1.ar.forEach(function (value2, index) {
              if (index === 0) {
                obj.singer = value2.name
              } else {
                obj.singer += '-' + value2.name
              }
            // console.log(obj.singer)
            })
            list.push(obj)
            this.playlist.songs = list
          })
        })
        .catch(() => {
          console.log('获取最新专辑数据失败')
        })
    } else if (this.$route.params.type === 'singer') {
      getArtistsSongs({ id: this.$route.params.id })
        .then((data) => {
        // console.log(data)
          this.playlist.name = data.artist.name
          this.playlist.image = data.artist.picUrl
          const list = []
          data.hotSongs.forEach((value1) => {
            const obj = {}
            obj.songID = value1.id
            obj.songName = value1.name
            obj.singer = ''
            value1.ar.forEach(function (value2, index) {
              if (index === 0) {
                obj.singer = value2.name
              } else {
                obj.singer += '-' + value2.name
              }
            // console.log(obj.singer)
            })
            // console.log(obj)
            list.push(obj)
            this.playlist.songs = list
          })
        // console.log(this.playlist)
        })
        .catch(() => {
          console.log('获取歌手歌曲数据失败')
        })
    } else if (this.$route.params.type === 'rank') {
      getPlayList({ id: this.$route.params.id })
        .then((data) => {
          // console.log(data)
          this.playlist.name = data.playlist.name
          this.playlist.image = data.playlist.coverImgUrl
          const list = []
          data.playlist.tracks.forEach((value1) => {
            // console.log(value1)
            const obj = {}
            obj.songID = value1.id
            obj.songName = value1.name
            obj.singer = ''
            value1.ar.forEach(function (value2, index) {
              if (index === 0) {
                obj.singer = value2.name
              } else {
                obj.singer += '-' + value2.name
              }
              // console.log(obj.singer)
            })
            list.push(obj)
            this.playlist.songs = list
          })
        })
        .catch(() => {
          console.log('获取排行版歌曲数据失败')
        })
    }
  },
  mounted: function () {
    const defaultHeight = this.$refs.top.$el.offsetHeight
    // console.log(defaultHeight)
    this.$refs.scrollView.scrolling((offsetY) => {
      // console.log(offsetY)
      if (offsetY < 0) {
        // console.log('向上滚动')
        // 高斯模糊效果是非常消耗性能的,不推荐在移动端使用
        // 如果非要使用,那么建议只设置一次
        // 可以使用蒙版效果
        // const scale = 20 * Math.abs(offsetY) / defaultHeight
        // this.$refs.top.$el.style.filter = `blur(${scale}px)`
        const scale = 0.15 + Math.abs(offsetY) / defaultHeight
        this.$refs.top.changeMask(scale)
      } else {
        // console.log('向下滚动')
        const scale = 1 + offsetY / defaultHeight
        // console.log(scale)
        this.$refs.top.$el.style.transform = `scale(${scale})`
      }
    })
  }
}
</script>

<style scoped lang="scss">
@import "../assets/css/mixin";
.detail{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  @include bg_sub_color();
  .bottom{
    position: fixed;
    top: 500px;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
</style>
